@import "module/variable";

@import "wordpress/editor-gutenberg";

.pfw {
  display: block;
}
.pfw-container {
  margin: 0 auto;
  padding: 0 15px;
  @media (min-width: $sm-min) {
    max-width: 500px;
  }

  @media (min-width: $md-min) {
    max-width: 720px;
  }

  @media (min-width: $lg-min) {
    max-width: 860px;
  }

  @media (min-width: $xl-min) {
    max-width: 1150px;
  }
}

.pfw-content {
  overflow: hidden;
  .alignfull {
    @media (min-width: $sm-min) {
      position: relative;
      left: 50%;
      width: 100vw;
      transform: translateX(-50%);
    }
  }
}